@import url(http://fonts.cdnfonts.com/css/optimus-princeps);
@import url(http://fonts.cdnfonts.com/css/acoustica-south);
@import url(https://fonts.googleapis.com/css2?family=Lora:wght@400;500;600;700&display=swap);
/*main stlyle shee*/
@tailwind base;
@tailwind components;
@tailwind forms;
@tailwind utilities;

.grecaptcha-badge { visibility: hidden; }

ul > li::marker{
    display: none;
}

ul {
    list-style-type: none;
    -webkit-padding-start: 0px;
            padding-inline-start: 0px;
}

* {
    font-family: 'Lora', serif;
    font-size: 1.07rem;
    font-smooth: antialiased;
    -webkit-font-smoothing: antialiased;
}

.btn-secondary:hover{
    background-color: rgba(0,0,0,0);
}

.section{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.menu .row{max-height:130px;overflow:hidden}.menu .row .col .media img{width:auto;max-height:130px;margin:0 auto}.site-heading{min-height:150px;width:100%;display:flex;flex-direction:row;flex-wrap:nowrap;justify-content:space-between}.site-heading-side{height:150px;width:100%;flex-grow:1;background-image:url(/static/media/top-3-min.b99b92fd.png);background-size:cover;background-position:top right}.site-heading-right{-webkit-transform:scaleX(-1);transform:scaleX(-1);-webkit-filter:FlipH;filter:FlipH;-ms-filter:"FlipH"}.site-heading-center{position:absolute;left:calc(50% - 75px);z-index:1;height:150px;min-width:150px;flex-grow:1;background-image:url(/static/media/top-4-min.41aaefd2.png);background-size:cover}
.menu{background-color:#fff;color:#763649}.menu .nav .nav-link{color:#763649}.menu .nav .nav-link:hover{cursor:pointer;color:#38141a}
.heading{margin:60px 0;margin-bottom:30px}.heading-text{font-family:"Acoustica South",sans-serif;font-size:2.75rem;font-weight:500;line-height:1.5;padding:0;color:#763649;display:flex;flex-wrap:wrap;align-items:center;justify-content:center}.heading-text span{font-family:inherit;font-size:inherit;font-weight:inherit;line-height:inherit}.heading-text--main{font-family:"Acoustica South",sans-serif;font-size:4.5rem;letter-spacing:1.3px;font-weight:500;line-height:1;padding:0;color:#763649;display:flex;flex-wrap:wrap;align-items:center;justify-content:center}.heading-text--main span{font-family:inherit;font-size:inherit;font-weight:inherit;line-height:inherit;margin-left:15px}.heading-subtext{display:inline-block;width:100%;text-align:center;font-size:.75rem;font-weight:500;line-height:1.5;padding:0;color:#763649}
.track-card{display:flex;flex-direction:column;align-items:center;justify-content:space-between;min-height:540px}.track-card-button{border:2px solid #67805a;color:#67805a}.track-card-button:hover{background-color:#213814}.track-card-body{display:flex;flex-direction:column;align-items:center;justify-content:space-between;padding:0px;min-width:100%}.track-card-body .track-card-preview{align-self:end;min-width:100%}.cover-preview{width:280px;height:280px;border-radius:5px}
.btn-remove{background-color:#763649;border:2px solid #38141a}.btn-remove:hover{background-color:#38141a}
.track-selector .btn-submit{border:2px solid #67805a;color:#67805a}
.text-block{line-height:2rem;color:#000;margin:0 auto;padding:0;max-width:550px;display:flex;flex-direction:column;justify-content:center;margin-bottom:30px}a{color:#67805a;text-decoration:none;text-align:center;display:block;justify-content:center}

.form-group{margin:0 auto;max-width:80%;margin-bottom:50px}.form-group label{font-weight:700}.sub-label{font-size:.6em;color:#763649;font-weight:700}.other-guests-container{border:1px dashed #bababa;padding:30px 10px;border-radius:5px;margin-bottom:30px}.other-guests-row{margin-bottom:20px}
.form-controls{padding:25px 50px;display:flex;flex-direction:row;justify-content:space-between;align-items:flex-end}
.btn{border-radius:0px;background-color:rgba(0,0,0,0)}.btn-next{align-self:flex-end;border:2px solid #67805a;color:#67805a}.btn-prev{align-self:flex-start;border:2px solid #67805a;color:#67805a}.btn-add-guest{border:2px solid #67805a;color:#67805a;width:100%}.btn-external{align-self:flex-start;border:2px solid #67805a;color:#67805a}.btn-disabled{background-color:#d3d3d3;color:gray;opacity:0}.btn-remove{border:2px solid #763649;color:#763649}.btn-right{align-self:flex-end}
.form{border:1px solid #bababa;margin:0 auto;box-shadow:10px 10px 40px rgba(0,0,0,.15);border-radius:5px 5px 5px 5px;overflow:hidden;margin-bottom:90px}
.list-item{padding:0;margin:0;list-style:none;font-size:16px;font-weight:600;margin-bottom:20px}.list-item::marker{display:none}
.site-footer{min-height:150px;width:90%;display:flex;flex-direction:row;flex-wrap:nowrap;justify-content:space-between;margin:0 auto}.site-footer-middle{height:241px;width:100%;flex-grow:1;background-image:url(/static/media/bottom-min.3a84a9f5.png);background-size:contain;background-repeat:no-repeat;background-position:center top}
